/* 
// 03.Components
*/
@function get-color($obj, $key) {
    @return map-get($map: $obj, $key: $key);
}

@each $key, $val in $theme-colors {
    .text-#{$key} {
        color: $val !important;
    }
    
    .btn-#{$key} {
        background: $val;
        border-color: $val;
        &:hover {
            background: darken($val, 5);
            border-color: darken($val, 5);
            opacity: 0.8;
        }
    }

}


.card-body {
    padding: 30px;
}

.breadcrumb {
    background: none;
    padding: 0;
    font-size: 13px;
    margin-bottom: 15px;
    ul {
        display: flex;
        align-items: center;
        margin-bottom: 0;
        li {
            position: relative;
            padding-right: 17px;
            margin-right: 8px;
            color: rgba($black, 0.6);
            &.active {
                color: $primary;
            }
            &:after {
                font-family: $primaryIcons;
                content: "\ecee";
                @include position(absolute, 0, -2px, 0, null);
                color: rgba($black, 0.6);
            }
            &:last-child {
                margin-right: 0;
                padding-right: 0;
                &:after {
                    display: none;
                }
            }
        }
    }
    a {
        color: rgba($black, 0.6);
        &:hover {
            color: rgba($black, 0.9);
        }
        &.active {
            color: $primary;
            &:hover {
                color: $primary;
            }
        }
    }
}

.btn {
    border-radius: $border-radius;
    padding: 12px 20px;
    transition: all 0.5s;
    border-radius: 4px;
    min-width: 100px;
    .btn-loader {
        display: none;
        @include position(absolute, 0, 0, 0, 0);
        align-items: center;
        justify-content: center;
    }
    &:focus {
        box-shadow: none !important;
    }
    &.btn-lg {
        padding: 10px 22px !important;
        font-size: 18px !important;
    }

    &.btn-sm {
        padding: 8px 16px !important;
        font-size: 13px !important;
    }

    &.btn-xs {
        padding: 5px 10px !important;
        font-size: 10px !important;
    }
}

.btn-primary-gred {
    background: $gred-btn-primary;
    border-color: transparent;
    background-size: 200% auto;
    box-shadow: none;
    color: $white;
    &:hover,
    &:focus,
    &.focus,
    &:active,
    &.active {
        background-position: right center;
        color: $white;
    }
}

.btn-white {
    background: $white;
    color: $primary;
    &:hover {
        background: $white;
        color: $primary;
    }
}

.btn-outline-white {
    background: transparent;
    color: $white;
    border-color: $white;
    &:hover {
        background: $white;
        color: $primary;
    }
}

.btn-outline-primary {
    color: $primary;
    border-color: $border-color;
    &:hover {
        background: $primary;
        color: $white;
    }
}

.pagination {
    justify-content: center;
    .page-item {
        margin-right: 5px;
        &:last-child {
            margin-right: 0;
            .page-link {
                border-radius: 100%;
            }
        }
        .page-link {
            border: none;
            width: 40px;
            height: 40px;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 100%;
            color: $default-color;
            &:hover {
                background: $light;
            }
        }
        &.active {
            a, span {
                background: $primary;
                color: $white;
                &:hover {
                    background: $primary;
                    color: $white;
                }
            }
        }
        &.disabled {
            opacity: 0.5;
        }
    }
}
