.blog {
  border-radius: 8px;
  overflow: hidden;
  background: $white;
  .blog-body {
    padding: 30px;
    .meta {
      display: flex;
      li {
        display: flex;
        align-items: center;
        i {
          color: $primary;
        }
        &:after {
          content: "|";
          display: inline-block;
          margin: 0 10px;
        }
        &:last-child:after {
          display: none;
        }
      }
    }
    h5 {
      a {
        color: $dark;
        font-weight: 500;
        &:hover {
          color: $primary;
        }
      }
    }
  }
}

.widget {
  padding: 30px;
  box-shadow: $box-shadow;
  border-radius: 8px;
  .widget-title {
    padding-bottom: 8px;
    border-bottom: 1px solid $dark;
    margin-bottom: 30px;
  }
}

.widget-search {
  .input-group {
    align-items: center;
    border: 1px solid $border-color;
    border-radius: 4px;
    .form-control {
      border: none;
    }
    .btn {
      margin: 0;
      border-top-left-radius: 0;
      border-bottom-left-radius: 0;
      padding: 0.25rem;
      background: #fff;
      min-width: 50px;
      display: flex;
      align-items: center;
      justify-content: center;
      &:hover {
        box-shadow: none;
      }
    }
  }
}

.widget-category-list {
  li {
    border-bottom: 1px solid $border-color;
    padding: 0.5rem 0;

    a {
      display: flex;
      justify-content: space-between;

      color: $default-color;
      &:hover {
        color: $primary;
      }
    }

    &:last-child {
      margin-bottom: 0;
      border-bottom: none;
    }
  }
}

.widget-post {
  .media {
    margin-bottom: 15px;

    img {
      width: 80px;
    }

    .media-body {
      margin-left: 0.75rem;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }

  .media-body {
    a {
      display: block;
      color: $default-color;
      font-weight: 600;

      &:hover {
        color: $primary;
      }
    }
  }
}

.insta-feeds {
  display: flex;
  margin: 0 -5px;
  flex-wrap: wrap;

  li {
    margin-bottom: 10px;
    padding: 0 5px;
    width: 33.33%;
    max-width: 33.33%;
    display: block;
    border-radius: 4px;
    overflow: hidden;

    a {
      display: block;
      transition: all 0.3s;
      &:hover {
        opacity: 0.8;
      }
    }
  }
}

.widget-tags {
  li {
    display: inline-block;
    margin-right: 0.5rem;
    margin-bottom: 0.25rem;
  }
}

.blog-content {
  .blockquote {
    border: 2px solid $primary;
    background-color: transparent;
    padding: 0 30px 30px;
    p {
      font-size: 18px;
    }
    h4 {
      font-weight: 500;
    }
    .quote-icon {
      font-size: 20px;
      color: $white;
      height: 45px;
      width: 45px;
      background-color: $primary;
      border-radius: 100%;
      margin-left: auto;
      margin-right: auto;
      display: flex;
      align-items: center;
      justify-content: center;
      margin-top: -23px;
      margin-bottom: 18px;
    }
  }
}

.tags-group {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;

  .tag-btn {
    font-size: 15px;
    line-height: 1;
    background-color: $light;
    color: $dark;
    padding: 15px;
    transition: all 0.2s ease-in-out;
    -webkit-transition: all 0.2s ease-in-out;
    margin-right: 15px;
    margin-bottom: 15px;
  }
}

.blog-footer-author {
  text-align: center;
  background-color: $light;
  position: relative;
  padding: 30px;

  .author-avatar {
    height: 120px;
    width: 120px;
    border-radius: 100%;
    overflow: hidden;
    margin: 0 auto;
    margin-bottom: 15px;
    margin-top: -80px;

    img {
      height: 100%;
      width: 100%;
      object-fit: cover;
    }
  }
}

.blog-footer-related {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  .related-item {
    flex: 1;

    a {
      color: $dark;
      transition: all 0.2s ease-in-out;
      -webkit-transition: all 0.2s ease-in-out;

      i {
        transition: all 0.2s ease-in-out;
        -webkit-transition: all 0.2s ease-in-out;
      }
    }

    img {
      width: 100px;
    }

    &:hover {
      a {
        color: $primary;

        i {
          margin-right: 1rem;
        }
      }
    }

    &.next {
      &:hover {
        a {
          i {
            margin-left: 1rem;
            margin-right: auto;
          }
        }
      }
    }
  }
}

.blog-footer-comment {
  .media > img {
    width: 50px;
    height: 50px;
    object-fit: cover;
  }

  .comment-header {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
  }
}

.flat-rounded-pagination {
  .page-item {
    &.disabled {
      .page-link {
        color: $default-color;
        pointer-events: none;
        cursor: auto;
        background-color: $white;
        border-color: $default-color;
      }
    }
    &.active,
    &:hover {
      .page-link {
        color: $white;
        background-color: $primary;
        border-color: $primary;
      }
    }
  }
  .page-link {
    border-radius: 30px !important;
    margin: 0 3px;
    border: none;
    width: 35px;
    height: 35px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: $dark;
  }
}
