/***** Google fonts import ************/


/*----------------------------------
* Base import's
----------------------------------*/
@import "variables";
@import "mixins";
@import "functions";
@import "components";
@import "blogs";

/*----------------------------------
* general css
----------------------------------*/
.container-fluid {
  width: 90%;
}

body {
  font-family: $font-primary;
  color: $default-color;
  font-size: px-rem-converter(14);
  line-height: px-rem-converter(24);
}

a {
  font-family: "Barlow", sans-serif;
  font-weight: 500;
  &:hover {
    text-decoration: none;
  }
}

strong {
  font-weight: 500;
}

h1,
h2,
h3,
h4,
h5,
h6, strong {
  font-family: "Barlow", sans-serif;
  color: $dark;
}

h1,
h2 {
  line-height: 1;
}

h3,
h4 {
  line-height: 1.25;
}

h5,
h6 {
  line-height: 1.4;
}

.rounded-pill {
  border-radius: 50rem !important;
}

.overlay {
  @include position(absolute, 0, 0, 0, 0);
  background: $black;
}

.mt-100 {
  margin-top: 100px;
}

.mt-30, .my-30 {
  margin-top: 30px;
}

.mb-30, .my-30 {
  margin-bottom: 30px;
}

.mb-50 {
  margin-bottom: 50px;
}

.pt-50, .py-50 {
  padding-top: 50px;
}

.pb-50, .py-50 {
  padding-bottom: 50px;
}

.pb-100 {
  padding-bottom: 100px;
}

.icon-sm {
  font-size: 14px;
}

.icon-md {
  font-size: 18px;
}

.icon-lg {
  font-size: 24px;
}

.icon-xl {
  font-size: 32px;
}

.form-group {
  margin-bottom: 30px;
}

.form-control {
  height: calc(2.9rem + 1px);
  border: 1px solid rgba($muted, 0.3);
  padding: 10px 15px;

  &:focus {
    box-shadow: none;
    border-color: $primary;
  }
}

.form-control-sm {
  height: calc(1em + .5rem + 2px);
  padding: .25rem .5rem;
}

.section {
  padding: 75px 0;
}

.box-shadow {
  box-shadow: $box-shadow;
}

.bg-gred {
  @include gradient($gred-primary);
}

.dropdown-menu {
  font-size: px-rem-converter(14);
  line-height: px-rem-converter(24);
}

.dropdown-item {

  &.active,
  &:active {
    background: none;
    color: $primary;
  }
}

.fakeLoader {
  display: block;
  background: $primary;
}

.number-list {
  list-style: decimal;
}

.social-icons {
  display: flex;
  align-items: center;
  a {
    width: 35px;
    height: 35px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 100%;
    @include border(null, 1px, solid, transparent);
    &:hover {
      background: $primary;
      color: $white;
    }
  }
  &.badge-icons{
    a {
      margin-right: 10px;
      background: rgba($primary, 0.1);
      color: $primary;
      border-color: $primary;
      &:hover {
        background: $primary;
        color: $white;
      }
    }
  }
}


.social-icon {
  li {
    display: inline-block;
    margin-right: 10px;
    a {
      color: $default-color;
      width: 36px;
      height: 36px;
      display: flex;
      align-items: center;
      justify-content: center;
      background: $light;
      border-radius: 100%;
      &:hover {
        color: $white;
        background: $primary;
      }
    }
  }
}

.search-btn {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: $primary;
  color: $white;
  border-radius: 100%;

  &:hover,
  &:focus {
    color: $white;
  }

  @media (max-width: 991px) {
    width: 35px;
    height: 35px;
  }
}

.header-search-dropdown {
  position: absolute;
  top: 5px;
  right: 0;

  @media (min-width: 991px) {
    top: 50%;
    @include transform(translateY(-50%))
  }

  .dropdown-menu {
    border: none;
  }
}

.search-box {
  input {
    padding-right: 40px;
    min-width: 225px;
    height: 40px;
    padding-left: 10px;
  }

  .icon {
    @include position(absolute, 0, 0, 0, null, 1);
    width: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: $white;
    background: $primary;
    border-top-right-radius: 0.25rem;
    border-bottom-right-radius: 0.25rem;
    box-shadow: none;
    border: none;
    cursor: pointer;
  }
}


.view-all {
  font-size: px-rem-converter(15);
  span {
    display: inline-block;
    @include transition(all 0.3s);
  }
  &:hover {
    span:last-child {
      padding-left: 5px;
    }
  }
}


/*----------------------------------
* shape css
----------------------------------*/
.divider {
  @include position(absolute, null, 0, -5px, 0);

  .fill-light-wrapper {
    fill: $white;
  }

  @media (max-width: 767px) {
    bottom: -8px;
  }
}

.bg-shape {
  position: absolute;
  bottom: -1px;
  left: 0px;
  right: 0px;
}

.bg-shape img {
  width: 100%;
}

/*----------------------------------
* banner css
----------------------------------*/

//header
.navbar {
  padding: 0;

  .navbar-brand {
    color: $black;
    display: block;
  }

  .logo-white {
    display: none;
  }


  .navbar-toggler {
    font-size: 24px;
    border: none;
    padding: 0;
    color: $black;

    &:focus {
      outline: none;
    }
  }

  .navbar-nav {
    .nav-link {
      line-height: 26px;
      color: $dark;
      @include transition(all 0.2s ease-in-out);
      padding: 0.5rem 1.25rem;
      position: relative;

      &:after {
        height: 25%;
        bottom: 10px;
        opacity: .3;
        left: 15px;
        right: 15px;
        content: "";
        position: absolute;
        @include transform(scaleX(0));
        transform-origin: 0% 50%;
        @include transition(all 0.5s cubic-bezier(0.2, 1, 0.3, 1));
      }

      &.active,
      &:hover,
      &:focus {
        color: $primary;

        &:after {
          @include transform(scaleX(1));
          background: $primary;
        }
      }

      &:active {
        color: $primary
      }
    }

    @media (max-width: 991px) {
      background: $white;
      margin-top: 15px;
    }
  }
}

.header {
  padding: 25px 0px;
  @include transition(all 0.5s ease-in-out);
  @include position(fixed, 0, 0, null, 0, 99);

  @media (max-width: 576px) {
    padding: 25px 15px;
  }

  &.fixed-nav {
    margin-top: 0px;
    padding: 15px 0px;
    background-color: $white;
    box-shadow: 0 10px 33px -14px rgba(0, 0, 0, .1);

    @media (max-width: 568px) {
      padding: 15px;
    }

    .navbar-toggler {
      color: $dark;
    }

    .logo-dark {
      display: block;
    }

    .logo-white {
      display: none;
    }

    .navbar-nav {
      .nav-link {
        color: rgba($black, 0.8);

        &.active,
        &:hover,
        &:focus {
          color: $primary;

          &:after {
            @include transform(scaleX(1));
            background: $primary;
          }
        }
      }

      .show>.nav-link {
        color: rgba($black, 0.8);

        &.active,
        &:hover,
        &:focus {
          color: $primary;

          &:after {
            @include transform(scaleX(1));
            background: $primary;
          }
        }
      }
    }
  }
}


.navbar-light {
  .navbar-toggler {
    color: $white;
  }

  .logo-dark {
    display: none;
  }

  .logo-white {
    display: block;
  }

  .navbar-nav {
    .nav-link {
      color: rgba($white, 0.8);

      @media (max-width: 991px) {
        color: rgba($dark, 0.8);
      }

      &.active,
      &:hover,
      &:focus {
        color: $white;

        @media (max-width: 991px) {
          color: rgba($dark, 0.8);
        }

        &:after {
          @include transform(scaleX(1));
          background: $white;

          @media (max-width: 991px) {
            background: $dark;
          }
        }
      }
    }

    .show>.nav-link {
      color: $white;

      @media (max-width: 991px) {
        color: rgba($dark, 0.8);
      }

      &:after {
        @include transform(scaleX(1));
        background: $white;

        @media (max-width: 991px) {
          background: $dark;
        }
      }
    }

    @media (max-width: 991px) {
      margin-top: 15px;
    }
  }
}

/*----------------------------------
* banner css
----------------------------------*/

.banner-title {
  background: rgba($primary, 0.95) url(#{$image-path}/banner-overlay.svg) 0 0 / cover;
  padding: 100px 0 130px;
  text-align: center;
  color: $white;
  position: relative;

  .divider {
    bottom: -1px;

    @media (max-width: 767px) {
      bottom: -5px;
    }

    img {
      width: 100%;
    }
  }

  .subtitle {
    color: $gray-200;

    @media (max-width: 767px) {
      display: none;
    }
  }

  @media (max-width: 767px) {
    padding: 70px 0 95px;
  }
}


/**
* ----------------------------------------------
* Footer css
* ----------------------------------------------
*/
footer {
  background: $footer-bg;
  padding-top: 100px;
  color: $muted;
  .f-logo {
    width: 150px;
    display: block;
  }

  a {
    color: $muted;
    @include transition(all 0.3s);
    &:hover {
      color: $white;
    }
  }
  .f-links {
    li {
      padding: 5px 0;
    }
  }
  h2 {
    color: $white;
    margin-bottom: 1rem;
    line-height: 1.5;
  }

  .copyright {
    margin-top: 30px;
    padding: 30px 0;
    width: 100%;
    @include border(top, 1px, solid, rgba($border-color, 0.1));
    text-align: center;
  }
}
